.status {
    margin: 1rem;
    color: cornflowerblue;
    border: 5px solid green;
    border-radius: 3px;
    font-size: 1.5rem;
    background-color: aliceblue;
    text-align: center;
    width: 25rem;
}

.error {
    margin: 1rem;
    color: red;
    border: 5px solid red;
    border-radius: 3px;
    font-size: 1.5rem;
    background-color: aliceblue;
    text-align: center;
    width: 25rem;
}

